*,
*::before,
*::after {
  box-sizing: border-box;
  position: relative;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 2vmin;
}

html {
  // background: #151515;
  // background-image: url("../images/throughTheDoors.png");
  // background: url('/./images/background.jpg');
  color: #fff;
  overflow: hidden;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.background {
  background-image: url("../images/throughTheDoors.png");
  background-size: cover;
  background-position: 50% 50%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 90%;
}

.slides {
  margin-top: 18%;
  margin-left: 22%;
  z-index: 2;
  display: grid;
  > .slide {
    grid-area: -1 / 1;
  }

  > button {
    appearance: none;
    background: transparent;
    border: none;
    color: white;
    position: absolute;
    font-size: 5rem;
    width: 5rem;
    height: 5rem;
    top: 30%;
    transition: opacity 0.3s;
    opacity: 0.7;
    z-index: 5;

    &:hover {
      opacity: 1;
    }

    &:focus {
      outline: none;
    }

    &:first-child {
      left: -10%;
    }
    &:last-child {
      right: 20%;
    }

    @media only screen and (max-width: 767px) {
      font-size: 15rem;
    }
    @media only screen and (max-width: 855px) {
      font-size: 10rem;
    }
  }
  @media only screen and (max-width: 855px) {
    margin-top: 65%;
    margin-left: 34%;
  }
}

